* {
  background-color: #F5F5F5;
}

.App {
  text-align: center;
}

.center-image {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #3e8e41;
}

.question {
  font-family: 'Lucida Calligraphy', cursive;
  font-size: 36px;
  color: #3e8e41;
  margin-bottom: 20px;
}
